export default Project;

// Each project card in the Projects section should have class="project-card" and the following:
// Image of the project
// Title : class="project-title"
// Description : class="project-description"
// Tech stack used : class="project-tech-stack"
// Link to GitHub repository : class="project-github-link"
// Deployed link or video link : class="project-deployed-link"

const DIV = styled.div`
  /* border: 1px solid red; */
  /* margin-top: 15px; */
  padding: 50px 0;
  /* background-color: white; */
  /* color: black; */

  .sub-title {
    margin-bottom: 50px;
    font-size: 60px;
    font-weight: 600;
    color: white;
  }

  .project-card {
    /* display: flex; */
    border-radius: 10px;
    position: relative;
    overflow: hidden;
    width: 100px; // 90%;
    margin: 50px auto;
  }

  /* .project-card {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  } */

  .project-card img {
    width: 100%;
    border-radius: 10px;
    display: block;
    transition: transform 0.5s;
  }

  .project-card:hover img {
    transform: scale(1.1);
  }

  .project-card:hover .layer {
    height: 100%;
  }

  .layer {
    padding: 0 140px;
    width: 100%;
    height: 0;
    background: linear-gradient(rgba(0, 0, 0, 0.6), #ff004f);
    border-radius: 10px;
    position: absolute;
    left: 0;
    bottom: 0;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    text-align: center;
    transition: height 0.5s;
  }

  .project-description,
  .project-tech-stack {
    text-align: left;
    font-weight: 600;
  }

  .layer h1,
  p {
    margin-bottom: 20px;
  }

  .button a {
    margin-left: 20px;
  }
`;
